<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <el-row>
                <el-col :span="8">
                    <el-input placeholder="">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="orderList" border stripe>
                <el-table-column type='index'></el-table-column>
                <el-table-column label='' prop='order_number'></el-table-column>
                <el-table-column label='' prop='order_price'></el-table-column>
                <el-table-column label='' prop='pay_status'>
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.pay_status ===0"></el-tag>
                        <el-tag type="danger" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label='' prop='is_send'></el-table-column>
                <el-table-column label=''>
                    <template slot-scope="scope">
                        {{scope.row.create_time | dataFormat}}
                    </template>
                </el-table-column>
                <el-table-column label=''>
                    <template>
                        <el-button size="mini" type="primary" icon="el-icon-edit" @click="showBox"></el-button>
                        <el-button size="mini" type="success" icon="el-icon-location" @click="showProgressBox"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="querInfo.pagenum"
                    :page-sizes="[5, 10, 15]"
                    :page-size="querInfo.pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="tabal">
                </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog
            title=""
            :visible.sync="ordessVisible"
            width="50%"
            @close='addressDialogClosed'
        >
            <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
                <el-form-item label="/" prop="addrss1">
                    <el-cascader :options="cityData" v-model="addressForm.addrss1"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="addrss2">
                    <el-input v-model="addressForm.addrss2"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="ordessVisible = false"> </el-button>
                <el-button type="primary" @click="ordessVisible = false"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
            title=""
            :visible.sync="ProgressVisible"
            width="50%"
        >
                <!--  -->
              <el-timeline>
                <el-timeline-item
                    v-for="(activity, index) in progressInfo"
                    :key="index"
                    :timestamp="activity.time">
                    {{activity.context}}
                </el-timeline-item>
            </el-timeline>
            <span slot="footer" class="dialog-footer">
            </span>
        </el-dialog>
    </div>
</template>

<script>
import cityData from './citydata'
export default {
    data() {
        return {
            querInfo: {
                query: '',
                pagenum: 1,
                pagesize: 10
            },
            tabal: 0,
            orderList: [],
            ordessVisible: false,
            addressForm: {
                addrss1: [],
                addrss2: ''
            },
            // 
            addressFormRules: {
                addrss1: [
                    { required: true, message: '/', trigger: 'blur' }
                ],
                addrss2: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            cityData,
            ProgressVisible: false,
            progressInfo: []
        }
    },
    created() {
        this.getOrderList()
    },
    methods: {
        async getOrderList() {
            const { data: res } = await this.$http('orders', { params: this.querInfo })
            if (res.meta.status !== 200) {
                return this.$message.error(res.meta.msg)
            }
            // this.$message.success(res.meta.msg)
            this.tabal = res.data.total
            this.orderList = res.data.goods
        },
        // size-change  
        handleSizeChange(newSize) {
            this.querInfo.pagesize = newSize
            this.getOrderList()
        },
        // current-change
        handleCurrentChange(newSize) {
            this.querInfo.pagenum = newSize
            this.getOrderList()
        },
        // 
        showBox() {
            this.ordessVisible = true
        },
        addressDialogClosed() {
            this.$refs.addressFormRef.resetFields()
        },
        // 
        async showProgressBox() {
            const { data: res } = await this.$http.get('/kuaidi/1106975712662')
            if (res.meta.status !== 200) {
                this.$message.error(res.meta.msg)
            }
            this.progressInfo = res.data
            this.ProgressVisible = true
        }
    }
}
</script>

<style lang="less" scoped>
.el-cascader {
    width: 100%;
}
</style>
